    <!-- ···························· DEV -->

    <v-container>
    <v-btn @click="Export(); forceRender()">Export data</v-btn>
    <debug :key="showInputData" v-show="Object.keys(inputData).length!=0">
        {{inputData}}
    </debug>
    </v-container>

    <!-- ···························· -->

    </wrap>
</template>

<script>
// ---------------------------- define export

    let props       = []
    let components  = {}
    let methods     = {}
    let computed    = {}
    let watch       = {}
    let data        = {}

// ---------------------------- main logic

    // use router
    computed = { ...computed, router ()   { return this.$router } }
    methods  = { ...methods,  goto (path) { return this.$router.push(path) } }


// ---------------------------- dev

    data = { ...data, e1:1, yesPlease:false, }
    data = { ...data, ratingP1:1, ratingP2:1, ratingP3:1, ratingS1:1, ratingS2:1, ratingS3:1 }
    data = { ...data, formFinished: false, showInputData: 0}
    data = { ...data, inputData: new Object}

    // create lists

    import EditableList    from "@/models/EditableList"
    const  YourIndustry    = new EditableList({constructor: EditableList})
    const  PrimaryFocusLOB = new EditableList({constructor: EditableList})
    const  SecondaryFocusLOB = new EditableList({constructor: EditableList})
    const  PrimaryBAs = new EditableList({constructor: EditableList})
    const  SecondaryBAs = new EditableList({constructor: EditableList})

    data = {
        YourIndustry,
        PrimaryFocusLOB,
        SecondaryFocusLOB,
        PrimaryBAs,
        SecondaryBAs,
    ...data }

    methods = {
        ExportAll () { return [
            YourIndustry.ExportList(),
            PrimaryFocusLOB.ExportList(),
            SecondaryFocusLOB.ExportList(),
        ]},
    ...methods }

    // load data

    import Content from "@/bazaar/content/content"

    const field_values = {
        YourIndustry:    Content.IndustryList(),
        PrimaryFocusLOB: Content.FocusLOBList(),
        SecondaryFocusLOB: Content.FocusLOBList(),
    }

    // apply data

    YourIndustry.CreateList({ ...field_values['YourIndustry'] })
    PrimaryFocusLOB.CreateList({ ...field_values['PrimaryFocusLOB'] })
    SecondaryFocusLOB.CreateList({ ...field_values['SecondaryFocusLOB'] })
    // load data deprecated way

    import ExampleList  from "@/models/ExampleList"

    //YourIndustry.CreateList({...ExampleList['YourIndustry']})
    //PrimaryFocusLOB.CreateList({...ExampleList['PrimaryFocusLOB']})
    PrimaryBAs.CreateList({...ExampleList['FirstBA']})
    SecondaryBAs.CreateList({...ExampleList['FirstBA']})

// ---------------------------- ui actions

    data = {
        ui_editor: {
            state: false,
            list:  false,
            input: '',
        },
    ...data }

    methods = {

        uiOpenListInEditor (list) {
            let RNA = this
            RNA.$data.ui_editor = { state:true, input:'', list }
        },

        uiOpenSublistInEditor () {
            let RNA = this

            if ( !RNA.uiIsSublistReady() ) return false

            let rowid = RNA.$data.PrimaryFocusLOB.uimodel
            const row = RNA.$data.PrimaryFocusLOB.GetItem(rowid)
            RNA.uiOpenListInEditor(row.child)
        },

        uiAddItemInEditor () {
            let RNA = this
            let label = RNA.$data.ui_editor.input
            let list  = RNA.$data.ui_editor.list

            if (!label || !list) return false

            // update model
            list.CreateItem({label})

            // update ui
            RNA.$data.ui_editor.input = ''   // reset text input
        },

        uiIsListOpen (list) {
            let   RNA = this
            const open = RNA.$data.ui_editor.state
            const data = RNA.$data.ui_editor.list

            // return class-name friendly
            if ( !open||!data ) return ''
            if ( !list )        return ''
            if (  list.caption !== data.caption ) return ''
            return 'is-open'
        },

        uiIsSublistReady () {
            let RNA = this
            return !!RNA.$data.PrimaryFocusLOB.uimodel
        },

        uiSwitchSublists (focusLOB, businessAreas) {
            let   RNA = this
            const FocusLOB = focusLOB
            const BAs = businessAreas
            // define list to change
            let rowid = FocusLOB.uimodel
            const row = FocusLOB.GetItem(rowid)
            if (!row.child) { // create new sublist
                 row.child = new FocusLOB.constructor({caption:row.label})
            }
            // reset ui state
            BAs.uimodel  = null
            // sync sublists
            BAs.rows     = row.child.rows || []
            BAs.current_caption  = row.child.caption || false
        },

        Export () {
            let   RNA = this
            let rowids
            let rows 

            const industryList = RNA.$data.YourIndustry
            let rowid = industryList.uimodel
            let row = industryList.GetItem(rowid)
            if(row)
            this.$data.inputData.YourIndustry = row.label

            //Primary
            const primaryLOB = RNA.$data.PrimaryFocusLOB
            rowid = primaryLOB.uimodel
            row = primaryLOB.GetItem(rowid)
            if(row){
                this.$data.inputData.PrimaryFocusLOB = {
                    name: row.label,
                    NeedForDifferentiation:this.$data.ratingP3,
                    HappinessWithExistingSolution: this.$data.ratingP2,
                    RadicalityOfFutureChange: this.$data.ratingP3,
                }

                const primaryBusinessAreas = RNA.$data.PrimaryBAs
                rowids = primaryBusinessAreas.uimodel
                if(rowids)
                rows = rowids.map(rowid => primaryBusinessAreas.GetItem(rowid))
                if(rows)
                this.$data.inputData.PrimaryFocusLOB ={
                    ...this.$data.inputData.PrimaryFocusLOB, BusinessAreas:rows.map(row => row.label)}
            }   

            //Secondary
            const secondaryLOB = RNA.$data.SecondaryFocusLOB
            rowid = secondaryLOB.uimodel
            row = secondaryLOB.GetItem(rowid)
            if(row){
                this.$data.inputData.SecondaryFocusLOB = {
                    name: row.label,
                    NeedForDifferentiation: this.$data.ratingS1,
                    HappinessWithExistingSolution: this.$data.ratingS2,
                    RadicalityOfFutureChange: this.$data.ratingS3
                }

                const secondaryBusinessAreas = RNA.$data.SecondaryBAs
                rowids = secondaryBusinessAreas.uimodel
                if(rowids)
                    rows = rowids.map(rowid => secondaryBusinessAreas.GetItem(rowid))
                if(rows)
                this.$data.inputData.SecondaryFocusLOB = {
                    ...this.$data.inputData.SecondaryFocusLOB, BusinessAreas:rows.map(row => row.label)}
            }
        },

        Finish(){
            return true;
        },

        forceRender(){
            this.$data.showInputData += 1;
        },
    ...methods }

// ---------------------------- export ready

export default {
    name: 'part-M-0-Form1',
    props,
    components,
    methods,
    computed,
    watch,
    data () { return data },
}

// ---------------------------- end script
</script>
